import {api} from 'ts/util/api';
import {Languages} from 'ts/util/Languages';

import {dateTime, format, request, string} from '/ts/util/utils';
import opg from '/ts/opg';
import {url} from '/ts/util/utils';
import Schedule from "./schedule";
import {store} from "/ts/util/store";

api({
	deleteSchedule: 'schedule/DelScheduleById',
});


template.defaults.imports.dateFormat = format.date;
template.defaults.imports.parseInt = parseInt;

//template.defaults.imports.log = console.log;

Schedule.fetch(function (fullObj) {

	document.getElementById('divSec').innerHTML = template('tpl', fullObj);


	const iframe = (request['iframe'] == 1);

	if (iframe) {
		document.body.style.cssText += 'background-color:#fff;'
	}

	const role = store.get('RoleName');
	const stage=store.get('Stage');

	$('#divSec').on('click', '.divMeeting,.divActivity', function () {
		let keys = this.getAttribute('data-key').split('/');
		let obj = fullObj.dayArr[keys[0]].time[keys[1]].acts[keys[2]];

		let d: AuiDialog = dialog({
			id: 'id-demo' + obj.activitycontent_id,
			align: 'right top',
			content: `	<h3>${obj.title}</h3>
					<p class="ab"><b class="red">${fullObj.lng.time}:</b>${obj.begindate.split(' ')[0]} ${obj.time}</p>
					<p class="ab"><b class="red">${fullObj.lng.location}:</b>${obj.location}</p>
					<p class="ab"><b class="red">${fullObj.lng.content}:</b>${obj.content}</p>			
					<p><a class="aView" href="javascript:view(${obj.activitycontent_id})">${fullObj.lng.viewDetail}</a></p>
					`,
			quickClose: true,// 点击空白处快速关闭
		});

		d.show(this);
	}).on('click', '.divAppoint,.divTempAppoint,.divAppointSpeed', function () {
		let keys = this.getAttribute('data-key').split('/');
		let obj = fullObj.dayArr[keys[0]].time[keys[1]].acts[keys[2]];


		if(stage=="open diary") {
            if (role === 'exhibition') {
                let d: AuiDialog  = window['current_dialog'] = dialog({
                    id: 'id-demo' + obj.activitycontent_id,
                    align: 'right top',
                    content: `	<h3>${obj.title || fullObj.lng.Appoint}</h3>
					<p class="ab"><b class="red">${fullObj.lng.time}:</b>${obj.begindate.substr(0, 16)} - ${obj.enddate.substr(11, 5)}</p>
					<p class="ab"><b class="red">${fullObj.lng.location}:</b>${obj.address}</p>
					<p class="ab"><b class="red">${fullObj.lng.buyer}:</b>${obj.name}</p>
					<p class="ab"><b class="red">${fullObj.lng.exhibition}:</b>${obj.company}</p>
                	<p><a class="aView" href="javascript:view2(${obj.barcode},'buyer')">${fullObj.lng.viewDetail}</a></p>
                	<p><button class="btn-danger" data-sid="${obj.scheduleid}" onclick="deleteEntry('${obj.scheduleid}')">${fullObj.lng.del}</button></p>`,
                    quickClose: true,// 点击空白处快速关闭
                });
                d.show(this);
            }
            else {
                let d: AuiDialog  = window['current_dialog'] = dialog({
                    id: 'id-demo' + obj.activitycontent_id,
                    align: 'right top',
                    content: `	<h3>${obj.title || fullObj.lng.Appoint}</h3>
					<p class="ab"><b class="red">${fullObj.lng.time}:</b>${obj.begindate.substr(0, 16)} - ${obj.enddate.substr(11, 5)}</p>
					<p class="ab"><b class="red">${fullObj.lng.location}:</b>${obj.booth_no}</p>
					<p class="ab"><b class="red">${fullObj.lng.buyer}:</b>${obj.name}</p>
					<p class="ab"><b class="red">${fullObj.lng.exhibition}:</b>${obj.company}</p>
                	<p><a class="aView" href="javascript:view2(${obj.barcode},'exhibition')">${fullObj.lng.viewDetail}</a></p>
                	<p><button class="btn-danger" data-sid="${obj.scheduleid}" onclick="deleteEntry('${obj.scheduleid}')">${fullObj.lng.del}</button></p>`,
                    quickClose: true,// 点击空白处快速关闭
                });
                d.show(this);
            }
		}
		else {
            if (role === 'exhibition') {
                let d: AuiDialog  = window['current_dialog'] = dialog({
                    id: 'id-demo' + obj.activitycontent_id,
                    align: 'right top',
                    content: `	<h3>${obj.title || fullObj.lng.Appoint}</h3>
					<p class="ab"><b class="red">${fullObj.lng.time}:</b>${obj.begindate.substr(0, 16)} - ${obj.enddate.substr(11, 5)}</p>
					<p class="ab"><b class="red">${fullObj.lng.location}:</b>${obj.address}</p>
					<p class="ab"><b class="red">${fullObj.lng.buyer}:</b>${obj.name}</p>
					<p class="ab"><b class="red">${fullObj.lng.exhibition}:</b>${obj.company}</p>
                    <p><a class="aView" href="javascript:view2(${obj.barcode},'buyer')">${fullObj.lng.viewDetail}</a></p>`,
                    quickClose: true,// 点击空白处快速关闭
                });
                d.show(this);
            }
            else {
                let d: AuiDialog  = window['current_dialog'] = dialog({
                    id: 'id-demo' + obj.activitycontent_id,
                    align: 'right top',
                    content: `	<h3>${obj.title || fullObj.lng.Appoint}</h3>
					<p class="ab"><b class="red">${fullObj.lng.time}:</b>${obj.begindate.substr(0, 16)} - ${obj.enddate.substr(11, 5)}</p>
					<p class="ab"><b class="red">${fullObj.lng.location}:</b>${obj.booth_no}</p>
					<p class="ab"><b class="red">${fullObj.lng.buyer}:</b>${obj.name}</p>
					<p class="ab"><b class="red">${fullObj.lng.exhibition}:</b>${obj.company}</p>
					<p><a class="aView" href="javascript:view2(${obj.barcode},'exhibition')">${fullObj.lng.viewDetail}</a></p>`,
                    quickClose: true,// 点击空白处快速关闭
                });
                d.show(this);
            }
		}
	}).on('click', '.divAddTo', function (e) {

		let target;
		if (role === 'exhibition') {
			target = __uri('../buyers/index.html');
		}
		else {
			target = __uri('../exhibitors/index.html');
		}
		let curTimes = e.target.getAttribute('data-time').split('-');
		let beginDateTime = e.target.getAttribute('data-date') + ' ' + curTimes[0];
		let endDateTime = e.target.getAttribute('data-date') + ' ' + curTimes[1];
		//console.log(beginDateTime, endDateTime);
        store.set('scheduleAddtoTime',beginDateTime+'|' + endDateTime);
		location.href = url.setParam(target, {
			from: 'schedule',
            info: `${beginDateTime} ~ ${endDateTime}`
		});

	});


	const pageSrc = __uri('../events/detail.html');
	window['view'] = function (id) {
		let src = url.setParam(pageSrc, {id});
		opg.popTop(`<iframe src="${src}"></iframe>`, {
			title: Languages.package.viewDetail,
			width: 500,
			height: 500
		});
	};

    const pageSrc2 = __uri('../wishlist/barcode.html');
    window['view2'] = function (barcode,type) {
        let src2 = url.setParam(pageSrc2, {barcode,type});
        opg.popTop(`<iframe src="${src2}"></iframe>`, {
            title: Languages.package.viewDetail,
            width: 950,
            height: 700
        });
    };

	api.deleteSchedule.set('onError', function (code, error, callback) {
		if (code === 200 && error === 'DelScheduleById_ERROR01') {
			opg.alert(Languages.package.deleteError_Max);
		}
	});
	window['deleteEntry'] = function (scheduleId) {
		window['current_dialog'].close();
		api.deleteSchedule({scheduleId}, res => location.reload(true));
	};

});
